.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  width: 100%;
  z-index: 1;
}

.header__wrapper {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 45px;
}

.header__link {
  margin-right: 160px;
  user-select: none;
  cursor: pointer;

  &:hover {
    opacity: 0.9;
  }

  &:focus,
  &:active {
    opacity: 0.7;
  }
}

.header__list {
  display: flex;
  gap: 120px;
  @include reset-list;
  margin-right: 164px;
  margin-top: -10px;
}

.header__item-link {
  display: flex;
  align-items: center;
  color: #f2f5f6;
  line-height: 18px;
  user-select: none;

  &:hover {
    opacity: 0.9;
  }

  &:focus,
  &:active {
    opacity: 0.7;
  }

  svg {
    margin-right: 15px;
  }
}

.header__request {
  font-family: $secondary-font;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #f2f5f6;
  padding: 11px 16px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  background-color: transparent;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-top: -10px;
  user-select: none;
}

@media (max-width: 1919px) {
  .header {
    position: static;
    width: auto;
    z-index: inherit;
  }

  .header__wrapper {
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 35px;
  }

  .header__link {
    margin: 0;
  }

  .header__list {
    flex-wrap: wrap;
    gap: 10px 40px;
    max-width: 50%;
    margin: 0;
    margin-top: 10px;
    margin-right: 30px;
  }

  .header__item-link {
    svg {
      margin: 0;
    }
  }

  .header__item-data {
    display: none;
  }

  .header__request {
    display: none;
  }
}

@media (max-width: 769px) {
  .header__wrapper {
    align-items: center;
    padding-top: 15px;
    padding-bottom: 30px;
  }
  .header__logo {
    width: 35px;
    height: 35px;
  }

  .header__list {
    gap: 40px;
    margin-right: 20px;
  }
}