.feedback {
  background-color: #00b3ff;
  background-image: url('../img/desktop/feedback-background.png');
  background-position: 0 -575px;
  background-size: 1920px, 1582px;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url('../img/desktop/feedback-background@2x.png');
    background-size: 1920px, 1582px;
  }
}

.feedback__wrapper {
  display: flex;
  padding-top: 170px;
  padding-bottom: 247px;

}

.feedback__contact {
  max-width: 580px;

  h2 {
    font-family: $secondary-font;
    font-size: 36px;
    line-height: 51px;
    font-weight: 400;
    color: #ffffff;
    margin: 0;
    margin-bottom: 6px;

    + p {
      line-height: 22px;
      color: #ffffff;
      margin: 0;
      margin-bottom: 14px;
    }
  }
}

.feedback__form {
  display: flex;
  flex-wrap: wrap;

  input {
    font-family: inherit;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    width: calc(100% / 2 - 15px);
    background-color: rgba(4, 20, 39, 0.15);
    border: none;
    border-radius: 3px;
    padding: 14px;

    &:first-of-type {
      margin-right: 30px;
    }

    &:hover,
    &:focus,
    &:active {
      background-color: rgba(4, 20, 39, 0.30);
    }

    &::placeholder {
      color: #ffffff;
    }
  }

  textarea {
    font-family: inherit;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    width: 100%;
    min-height: 155px;
    margin-top: 20px;
    margin-bottom: 14px;
    background-color: rgba(4, 20, 39, 0.15);
    border: none;
    border-radius: 3px;
    padding: 25px 15px;
    resize: none;

    &:hover,
    &:focus,
    &:active {
      background-color: rgba(4, 20, 39, 0.30);
    }
  
    &::placeholder {
      color: #ffffff;
    }
  }

  button {
    font-family: $secondary-font;
    font-size: 16px;
    line-height: 19px;
    font-weight: 700;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    background-color: #041427;
    color: #ffffff;
    padding: 13px 25px;
    margin-bottom: 14px;
    min-width: 265px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    user-select: none;

    &:hover,
    &:focus,
    &:active {
      background-color: #042327;
    }
  }

  p {
    margin: 0;
    width: 100%;
    user-select: none;

    label {
      display: block;
      width: 234px;
      padding-left: 26px;
      font-size: 14px;
      line-height: 16px;
      color: #FFFFFF;
      opacity: 0.8;
      position: relative;
      cursor: pointer;

      &:hover {
        &::before {
          border-color: #f2f2f2;
        }
      }

      &::before,
      &::after {
        content: '';
        position: absolute;
        transform: translateY(-50%);
        top: 50%;
      }

      &::before {
        width: 15px;
        height: 15px;
        border: 1px solid #ffffff;
        border-radius: 2px;
        left: 0;
      }
    }

    input {
      &:checked {
        ~ label {
          &::after {
            width: 9px;
            height: 6px;
            background-image: url('../img/icons/icon-check.svg');
            left: 3px;
          }
        }
      }
    }
  }
}

.feedback__picture {
  order: -1;
  margin-right: 115px;
  margin-top: 76px;
}

@media (max-width: 1919px) {
  .feedback {
    background-image: none;
  }

  .feedback__wrapper {
    padding-top: 45px;
    padding-bottom: 111px;
    max-width: 770px;
    margin: 0 auto;
  }

  .feedback__contact {
    max-width: none;
    width: 100%;

    h2 {
      margin-bottom: 7px;

      + p {
        margin-bottom: 25px;
      }
    }
  }

  .feedback__form {
    textarea {
      margin-bottom: 35px;
    }

    button {
      margin-right: 85px;
    }

    p {
      width: auto;
      margin-top: 10px;
    }
  }

  .feedback__picture {
    display: none;
  }
}

@media (max-width: 769px) {
  .feedback__wrapper {
    padding-bottom: 88px;

    h2 {
      font-size: 30px;
      line-height: 140.62%;
      margin-bottom: 10px;

      + p {
        font-size: 14px;
        line-height: 134.4%;
        margin-bottom: 34px;
      }
    }
  }

  .feedback__form {
    input {
      width: 100%;
      margin-bottom: 20px;

      &:first-of-type {
        margin-right: 0;
      }
    }

    textarea {
      margin-top: 0;
      margin-bottom: 30px;
      min-height: 180px;
    }

    button {
      margin-right: 0;
      margin-bottom: 25px;
      width: 100%;
    }

    p {
      margin: 0 auto;

      label {
        width: 100%;
        margin-left: 5px;
      }

      input {
        width: auto;
      }
    }
  }
}